* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
    text-decoration: none;
}
body {
    background-color: #87b5b5;
}
img {
    width: 100%;
}
.shadow-block {
    box-shadow: 0px 0px 1rem 0px #111111;
}
.editor {
    width: 50%;
    margin: auto;
    margin-top: 3rem;
}
.previewer {
    width: 70%;
    margin: auto;
    margin-top: 1rem;
}
textarea {
    display: block;
    width: 100%;
    height: 15rem;
    font-size: larger;
    padding: 0.8rem;
    background-color: #c0d8d8;
    resize: vertical;
}
#preview {
    overflow: hidden;
    background-color: #c0d8d8;
    padding: 3rem;
}
.expand {
    min-height: 90vh;
    height: fit-content;
}
.none {
    display: none;
}
@media screen and (max-width: 576px) {
    .editor {
        width: 80%;
    }
    :root {
        font-size: xx-small;
    }
    .previewer {
        width: 95%;
    }
    #preview {
        padding: 1rem;
    }
}
